import { gsap } from 'vendors/gsap';

export default class ListAnimation implements AnimationInterface {
  el?:HTMLElement;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {
      const entries = this.el.querySelectorAll('.entry'); 
      const borders = this.el.querySelectorAll('.border'); 
      
      if (!this.tl) {
        this.tl = gsap.timeline(
          { 
            paused: true,
            autoRemoveChildren: false,
            repeatRefresh: true,
          }
        )

        if (entries.length) {
          this.tl?.add(
            gsap.fromTo(entries, 
              { alpha: 0, x: -50 },
              { alpha: 1,  x: 0, duration: 0.4,  stagger: { amount: 0.75 }, ease: 'power2.out' },
            )
          )
        }
        if (borders.length) {
          this.tl?.add(
            gsap.fromTo(borders, 
              { scaleX: 0 },
              { scaleX: 1, duration: 0.4, stagger: { amount: 0.75 }, ease: 'power2.out' }
            ),
            '0'
          )
        }
      }
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0)
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }

  update = () => {
    // 
  }

  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse()
    }
  }

  dispose() {
    this.tl?.kill();
    this.tl = undefined;
  }
}
